
import PlateType from '../../../js/enum.js'
import { AjaxHttp, AjaxSilentHttp, AjaxFullSilentHttp } from '../../../js/ajaxHttp'
import { mapState } from 'vuex'
import Popup from 'vux/src/components/popup/index.vue'

import { getImageUrl, objToString, isArrayType } from "../../../js/utility.js"
import { APP_BUILD, PLATFORM } from '../../../config.js'
import $ from 'jquery'

import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
components: {
Grid,
GridItem,
XHeader,
Group,
Popup
},
computed: {
...mapState({
crmDetail: state => state.crmDetail,
userInfo: state => state.userInfo
}),
itemalllist() {
var tempAllList = {};
if (this.userInfo.leads || this.userInfo.admin === 1) {
tempAllList['leads'] = {
title: '线索',
type: PlateType.Leads,
field: '',
path: 'list/' + PlateType.Leads + '/default',
url: require('../../../assets/xs.png')
};
}
if (this.userInfo.customer || this.userInfo.admin === 1) {
tempAllList['customer'] = {
title: '客户',
field: 'customer',
type: PlateType.Customer,
path: 'list/' + PlateType.Customer + '/default',
url: require('../../../assets/index_customer.png')
};
tempAllList['customerpool'] = {
title: '客户池',
field: 'customerpool',
type: PlateType.CustomerPool,
path: 'list/' + PlateType.CustomerPool + '/default',
url: require('../../../assets/khc.png')
};
}
if (this.userInfo.contacts || this.userInfo.admin === 1) {
tempAllList['contacts'] = {
title: '联系人',
field: 'contacts',
type: PlateType.Contacts,
path: 'list/' + PlateType.Contacts + '/default',
url: require('../../../assets/lxr.png')
};
}
if (this.userInfo.business || this.userInfo.admin === 1) {
tempAllList['business'] = {
title: '商机',
field: 'business',
type: PlateType.Business,
path: 'list/' + PlateType.Business + '/default',
url: require('../../../assets/sj.png')
};
}
if (this.userInfo.contract || this.userInfo.admin === 1) {
tempAllList['contract'] = {
title: '合同',
field: 'contract',
type: PlateType.Contract,
path: 'list/' + PlateType.Contract + '/default',
url: require('../../../assets/sht.png')
};
}
if (this.userInfo.product || this.userInfo.admin === 1) {
tempAllList['product'] = {
title: '产品',
field: 'product',
type: PlateType.Product,
path: 'list/' + PlateType.Product + '/default',
url: require('../../../assets/cp.png')
};
}
if (this.userInfo.finance || this.userInfo.admin === 1) {
tempAllList['finance'] = {
title: '财务',
field: 'finance',
type: PlateType.Finance,
path: 'financemain',
url: require('../../../assets/caiwu.png')
};
}

return tempAllList;
}
},
data() {
return {
itemlist: [],
price: {
sum_price: "0",
sum_price_month: "0",
sum_price_week: "0",
sum_price_year: "0",
},
customer_count: {
week: "0",
month: "0",
year: "0"
},
contacts_count: {
week: "0",
month: "0",
year: "0"
},
business_count: {
week: "0",
month: "0",
year: "0"
},
log_count: {
week: "0",
month: "0",
year: "0"
},
mylog_count: {
week: "0",
month: "0",
year: "0"
},
all_role: {},
role_show_arrray: [],
p: 0, //数据从0开始

updateShow: false,
updateContent: '',
updateUrl: '',
}
},
mounted() {
var wkmoresort = window.localStorage.wkmoresort;
if (wkmoresort) {
var sortArray = wkmoresort.split(",");
for (var i = 0; i < sortArray.length; i++) {
var item = sortArray[i];
if (this.itemalllist[item]) {
this.itemlist.push(this.itemalllist[item]);
}
}
} else {
for (var key in this.itemalllist) {
if (this.itemlist.length < 7) {
this.itemlist.push(this.itemalllist[key]);
}
}
}


this.itemlist.push({
title: '更多',
type: 'more',
path: '',
url: require('../../../assets/more.png')
});

if (PLATFORM !== 'web') { //只有移动端有检查更新
this.getUpdateInfo(); 
}

if (this.crmDetail !== '') {
this.price = this.crmDetail.price;
this.customer_count = this.crmDetail.customer_count;
this.contacts_count = this.crmDetail.contacts_count;
this.business_count = this.crmDetail.business_count;
this.log_count = this.crmDetail.log_count;
this.mylog_count = this.crmDetail.mylog_count;
this.all_role = this.crmDetail.all_role;
this.role_show_arrray = this.crmDetail.role_show_arrray;
this.p = this.crmDetail.p;
}
this.getInfo();
},
beforeRouteLeave(to, from, next) {
this.$store.commit('updateCrmDetailInfo', {}); 
next();
},
methods: {
getInfo() {
AjaxSilentHttp('POST', 'm=index&a=index', {}, this, function(res, wself, success) {
if (success) {
wself.handleRes(res);
}
});
},
handleRes(res) {
this.price.sum_price = res.data.sum_price;
this.price.sum_price_month = res.data.sum_price_month;
this.price.sum_price_week = res.data.sum_price_week;
this.price.sum_price_year = res.data.sum_price_year;

this.customer_count = res.data.customer_count;
this.contacts_count = res.data.contacts_count;
this.business_count = res.data.business_count;
this.log_count = res.data.log_count;
this.mylog_count = res.data.mylog_count;

this.handleRoleArray(res.data.all_role_array);

this.$store.commit('updateCrmDetail', {
price: this.price,
customer_count: this.customer_count,
contacts_count: this.contacts_count,
business_count: this.business_count,
log_count: this.log_count,
mylog_count: this.mylog_count,
all_role: this.all_role,
role_show_arrray: this.role_show_arrray,
p: this.p,
});
},
handleRoleArray(all_role_array) {
var index = 0;

var childArray = [];
var all_role = [];
for (var i = 0; i < all_role_array.length; i++) {
var item = all_role_array[i];
item.role_info.thumb_path = getImageUrl(item.role_info.thumb_path);
childArray.push(item);

if (childArray.length === 3) {
all_role[index.toString()] = childArray;
index = index + 1;
childArray = [];
} else if (childArray.length < 3 && (all_role_array.length - 1) === i) {
all_role[index.toString()] = childArray;
}

}
this.all_role = all_role;
this.getPkList();
},
handlePk(type) {
if (type === 'left') {
if (this.p <= 0) {
this.p = 0;
return;
} else {
this.p = this.p - 1;
}
if (!this.getPkList()) {
this.p = this.p + 1;
}
} else if (type === 'right') {
this.p = this.p + 1;
if (!this.getPkList()) {
this.p = this.p - 1;
}
}
},
getPkList() {
var list = this.all_role[this.p.toString()];
if (isArrayType(list)) {
this.role_show_arrray = list;
return true;
} else {
return false;
}
},
enterPersonInfo(role_id) {
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
getUpdateInfo() {

AjaxFullSilentHttp('POST', 'http://www.crm.com/index.php?m=upgrade&a=checkMobileVersion', { params: JSON.stringify({ "channel": "CRM2", "type": this.isAndroid() ? "1" : "2", "versioncode": APP_BUILD }) }, this, function(res, wself, success) {
if (success) {
wself.$vux.loading.hide();
if (typeof res.data === 'object') {
wself.updateContent = res.data.updatemessage;
wself.updateUrl = res.data.url;
wself.updateShow = true;
}
}
});
},
goUpdate() {
window.open(this.updateUrl, '_system');
},
isAndroid() {
var u = navigator.userAgent;
return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
},
clickCRMItem(item) {
if (item.type !== 'more') { //不是更多按钮 按path跳转
this.$router.push({
path: item.path
})
} else {
this.$router.push({
name: 'moresort'
})
}
}
}
}




